.u-center-text {
	text-align: center;
}

.u-margin-bottom-small {
	margin-bottom: 2rem;
}

.u-margin-bottom-medium {
	margin-bottom: 4rem;
}

.u-margin-bottom-big {
	margin-bottom: 8rem;
}

.u-margin-top-big {
	margin-top: 8rem;
}

.u-font-size-large {
	font-size: 8rem;
}
.u-font-size-medium {
	font-size: 6rem;
}
.u-font-size-small {
